const routes = [
  {
    path: "/",
    component: DashboardLayout,
    redirect: "/home",
    children: [
      {
        path: "dashboard",
        name: "dashboard",
        component: Dashboard,
      },
      {
        path: "users",
        name: "users",
        component: User,
      },
      {
        path: "notifications",
        name: "notifications",
        component: Notifications
      },
      {
        path: "icons",
        name: "icons",
        component: Icons
      },
      {
        path: "maps",
        name: "maps",
        component: Maps
      },
      {
        path: "typography",
        name: "typography",
        component: Typography
      },
      {
        path: "table-list",
        name: "table-list",
        component: TableList
      },
      {
        path: "upload-photo",
        name: "upload-photo",
        component: UploadPhoto
      },
      {
        path: "profile",
        name: "profile",
        component: UserProfile
      },
      {
        path: "company",
        name: "company",
        component: Company
      },
      {
        path: "plans",
        name: "plans",
        component: Plan
      },
      {
        path: "drones",
        name: "drones",
        component: Drone
      },
      {
        path: "inspection",
        name: "inspection",
        component: Inspection
      },
      {
        path: "gallery",
        name: "gallery",
        component: Gallery
      },
      {
        path: "pilot",
        name: "pilot",
        component: Pilot
      },
    ]
  },
  {
    path: "/home",
    name: "home",
    component: Home
  },
  {
    path: "/login",
    name: "login",
    component: Login,
  },
  {
    path: "/register",
    name: "register",
    component: PlanRegister
  },
  {
    path: "/prices",
    name: "prices",
    component: Prices
  },
  {
    path: "/pay-response",
    name: "pay-response",
    component: ResponsePay
  },
  {
    path: "/pay",
    name: "pay",
    component: Pay,
    beforeEnter: (to, from, next) => {
      let plan = to.params.plan
      if(store.state.jwt != null && typeof plan  !== "undefined" && plan != null){
        next()
      }else{
        next({ name: 'register',  params: {'from':'pay'} })
      }
    }
  },
  { path: "*", component: NotFound }
];

/**
 * Asynchronously load view (Webpack Lazy loading compatible)
 * The specified component must be inside the Views folder
 * @param  {string} name  the filename (basename) of the view to load.
function view(name) {
   var res= require('../components/Dashboard/Views/' + name + '.vue');
   return res;
};**/

export default routes;
